<template>
  <div class="container mx-auto mt-5">
    <div class="p-5 my-3 shadow bg-gray-50">
      Lorem ipsum dolor sit amet consectetur adipisicing elit. Recusandae sed
      ipsum et? Sunt nesciunt magni explicabo ipsam dolore modi asperiores,
      voluptas nam ex similique minus ullam enim totam aliquam omnis.
    </div>
    <h2 class="my-3 text-3xl font-bold text-center dark:text-white">
      Meetings
    </h2>
    <div class="grid grid-cols-1 gap-3 md:grid-cols-2">
      <div
        @click="toggleModal"
        class="flex flex-col items-center justify-center p-10 transition-all duration-300 transform bg-green-300 rounded cursor-pointer  hover:text-white hover:bg-blue-600 hover:scale-105"
      >
        <img src="../assets/add.png" class="w-20" />
        <p class="my-2">Request a new</p>
        <h2 class="text-2xl">Advisory Meeting</h2>
      </div>
      <div v-for="(m, i) in meetings" :key="i" class="bg-gray-200 rounded">
        <div class="p-3">
          <h3 class="text-center">Date &amp; Time</h3>
          <h2 class="my-3 text-center indigo--text">
            {{
              m.datetime
                ? new Date(m.datetime).toDateString() +
                  " " +
                  new Date(m.datetime).toLocaleTimeString()
                : ""
            }}
          </h2>
          <h3 class="my-3 text-center">
            Status: {{ m.status ? "Approved" : "Pending" }}
          </h3>
          <div class="my-2 space-y-3">
            <div class="p-2 text-right" v-if="m.reason">
              <p class="inline p-2 text-white bg-green-500 rounded-full">
                {{ m.reason }}
              </p>
            </div>
            <div class="text-left" v-if="m.reply">
              <p class="inline p-2 text-white bg-blue-500 rounded-full">
                {{ m.reply }}
              </p>
            </div>
          </div>

          <div class="mt-4 text-center">
            <a
              :href="m.link"
              target="_blank"
              class="inline-block px-6 py-3 mx-auto mt-3 mb-3 mr-1 text-sm font-bold text-green-500 uppercase duration-150 ease-linear bg-transparent border border-green-500 border-solid outline-none  ransition-all hover:bg-green-500 hover:text-white bg-gray-50 active:bg-green-600 focus:outline-none"
              v-if="m.link"
            >
              <i class="fas fa-video"></i>
              {{
                new Date(m.datetime).getTime() > Date.now()
                  ? "Meeting"
                  : "Recorded"
              }}
              Link
            </a>
            <p class="text-center" v-else-if="m.status">
              Meeting link will be available soon
            </p>
          </div>
        </div>
      </div>
    </div>
    <Modal title="Request an Advisory Meeting">
      <template v-slot:body>
        <vee-form
          :validation-schema="meetingValidationSchema"
          @submit="meetingRequest"
        >
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white"
              >Suggested Time (Not fixed)</label
            >
            <vee-field
              type="datetime-local"
              name="date"
              class="form-control"
              placeholder="Meeting date"
            />
            <ErrorMessage class="text-red-600" name="date" />
          </div>
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white">Reason</label>
            <vee-field
              as="textarea"
              name="reason"
              class="form-control"
              placeholder="Meeting reason"
            />
            <ErrorMessage class="text-red-600" name="reason" />
          </div>
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white"
              >Meeting Duration</label
            >
            <vee-field
              as="select"
              name="duration"
              @change="amountCalculate"
              v-model="meeting.duration"
              class="form-control"
              placeholder="Meeting Duration"
            >
              <option value="" selected disabled>Select Duration</option>
              <option v-for="(t, i) in durations" :key="i" :value="t.value">
                {{ t.text }}
              </option>
            </vee-field>
            <ErrorMessage class="text-red-600" name="duration" />
          </div>
          <div class="mb-3">
            <label class="inline-block mb-2 dark:text-white"
              >Payable Amount</label
            >
            <input readonly class="form-control" :value="meeting.amount" />
          </div>
          <div class="text-center">
            <button
              :disabled="loading"
              type="submit"
              class="px-6 py-3 mx-auto mt-3 mb-1 mr-1 text-sm font-bold text-green-500 uppercase transition-all duration-150 ease-linear bg-transparent border border-green-500 border-solid outline-none  hover:bg-green-500 hover:text-white active:bg-green-600 focus:outline-none"
            >
              {{ loading ? "Requesting..." : "Submit Request" }}
            </button>
          </div>
        </vee-form>
      </template>
    </Modal>
  </div>
</template>

<script>
// import axios from "axios";
import { mapState, mapMutations } from "vuex";
import Modal from "@/components/Modal";
export default {
  components: { Modal },
  data() {
    return {
      meeting: {},
      loading: false,
      meetings: [],
      mrq: false,
      mtype: "",
      meetingValidationSchema: {
        date: "required",
        reason: "required",
      },
      durations: [
        { text: "30 minuntes", value: 30 },
        { text: "45 minutes", value: 45 },
        { text: "1 Hour", value: 60 },
        { text: "1 Hour 30 minutes", value: 90 },
        { text: "2 Hours", value: 120 },
      ],
    };
  },
  methods: {
    ...mapMutations(["toggleModal"]),
    amountCalculate() {
      this.meeting.amount = parseInt(this.meeting.duration) * 10;
    },
    meetingRequest(values) {
      this.loading = true;

      console.log(
        JSON.stringify({
          type: "create",
          roll: this.user.displayName,
          amount: parseInt(this.meeting.amount),
          ...values,
        })
      );

      // axios
      //   .post("http://localhost:3030/init", {
      //     type: "create",
      //     roll: this.user.displayName,
      //     amount: parseInt(this.meeting.amount),
      //     ...values,
      //   })
      //   .then((res) => {
      //     window.open(res.data.url, "_blank");
      //     this.loading = false;
      //     this.toggleModal();
      //   })
      //   .catch((err) => {
      //     alert(err);
      //     this.loading = false;
      //   });
    },
    getMeetings() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxCVghzqN-sCd9bsd9i8zlyrgmyMdDaNtqcjwygaZnsM-0FQCXtkRdohybS8uThVA_Q/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          let meetings = data.routines.filter(
            (k) =>
              k.roll === this.user.displayName ||
              (k.type == "all" &&
                JSON.parse(k.roll).find((h) => {
                  console.log(h);
                  return h.roll == this.user.displayName;
                }))
          );
          this.meetings = meetings.sort((a, b) => b.meeting_id - a.meeting_id);
        });
    },
  },
  created() {
    this.getMeetings();
  },
  computed: {
    ...mapState(["user"]),
  },
};
</script>

<style lang="scss" scoped>
</style>