<template>
  <div>
    <div
      v-if="showModal"
      class="fixed inset-0 z-50 flex items-center justify-center overflow-x-hidden overflow-y-auto outline-none  focus:outline-none"
    >
      <div class="relative w-full mx-auto my-6 md:mx-2 md:w-1/2 lg:w-1/3">
        <!--content-->
        <div
          class="relative flex flex-col w-full bg-white border-0 shadow-lg outline-none  dark:text-white -lg focus:outline-none dark:bg-gray-700"
        >
          <!--header-->
          <div
            class="flex items-start justify-between p-5 border-b border-solid  -t border-blueGray-200"
          >
            <h3 class="text-2xl font-semibold">{{ title }}</h3>
            <button
              class="flex items-center justify-center float-right w-6 h-6 p-4 ml-auto text-3xl font-semibold leading-none transform bg-red-500 border-0 rounded-full outline-none  hover:scale-105 focus:outline-none"
              v-on:click="toggleModal"
            >
              <span
                class="block text-2xl text-white outline-none  -full focus:outline-none"
              >
                ×
              </span>
            </button>
          </div>
          <!--body-->
          <div class="relative flex-auto p-6">
            <slot name="body" />
          </div>
          <!--footer-->

          <slot name="action" />
        </div>
      </div>
    </div>
    <div v-if="showModal" class="fixed inset-0 z-40 bg-black opacity-25"></div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "small-modal",
  props: { title: { type: String, required: true } },
  methods: {
    ...mapMutations(["toggleModal"]),
  },
  computed: {
    ...mapState(["showModal"]),
  },
};
</script>